.dialogs {
    display: flex;
}
.dialogItems {
    padding-right: 20px;
    padding-top: 20px;
    margin-left: 20px;
    border-right: 1px solid black;
    height: 100vh;
}

.messagesItems{
    min-height: 30px;
    margin-top: 20px;
    font-size: 20px;
    margin-left: 20px;
}
.messages {
    width:auto;
    border: 2px solid black;
    border-radius: 10px;
    margin-top: 25px;
    background: cornsilk;
}
.block {
    display: flex;
    font-size: 20px;
}
.avatar {
    width: 50px;
    border-radius: 25px
}
.sendmessageblock{
    display: flex;
    justify-content: flex-start;
    margin-top: 20px;
}